<template>
	<div class="home">
		<el-row id="artList" type="flex" justify="space-around">
			<el-col :span="16">
				<el-row class="art-item" v-for="(item, index) in data" :key="item._id">
					<el-card shadow="hover">
						<h5><router-link :to="'/article/'+ item._id" tag="span" class="art-title">{{item.title}}</router-link></h5>
						<el-row class="art-info d-flex align-items-center justify-content-start">
							<!-- <div class="art-time"><i class="el-icon-time"></i>：2019-03-24</div> -->
							<div 
								class="d-flex align-items-center"
							>
								<img class="tag" src="~assets/tag.png"/>：
								<el-tag size="mini" class="tag" v-for="(item, index) in item.categories" :key="index">{{item.name}}</el-tag>
							</div>
						</el-row>
						<el-row class="art-body">
							<!-- <div class="side-img hidden-sm-and-down"><img class="art-banner" src="~assets/vue.jpg"></div> -->
							<div class="side-abstract">
								<div class="art-abstract">
									{{item.abstract}}
								</div>
								<div class="art-more">
									<router-link :to="'/article/'+ item._id" tag="span">
										<el-button plain>阅读全文</el-button>
									</router-link>
									<!-- <div class="view"><i class="el-icon-view"></i>12414</div> -->
								</div>
							</div>
						</el-row>
					</el-card>
					<img class="star" src="~assets/star.png" v-if="index === 0"/>
				</el-row>
				
				<div class="block pagination">
					<el-pagination 
						:background="true" 
						layout="prev, pager, next" 
						:total="total" :pageSize="5" 
						:current-page.sync="current"
						@current-change="handleChange"
					>
					</el-pagination>
				</div>
			</el-col>
			<el-col :span="6" class="hidden-sm-and-down" id="side">
				<div class="item">
					<tag></tag>
				</div>
			</el-col>
		</el-row>

	</div>
</template>

<script>
	import {getTotal, getArticles} from 'network/home.js'
	import Tag from 'components/tag.vue'
	export default {
		name: 'Home',
		created() {
			getTotal().then((res) => {
				this.total = res.total
			})
			this.getArticleList(this.current)
		},
		data() {
			return {
				total: 0,
				current: 1,
				data: []
			}
		},
		methods: {
			getArticleList(current){
				getArticles(current).then((res) => {
					this.data = res
				})
			},
			handleChange(){
				this.getArticleList(this.current)
			}
		},
		components: {
			Tag
		}
	}
</script>

<style scoped>
	#side .item {
		margin-bottom: 30px;
	}
	
	.art-item {
		margin-bottom: 30px;
		position: relative;
	}
	
	.art-item .star {
		width: 60px;
		height: 60px;
		position: absolute;
		top: 0;
		right: 0;
	}
	
	img.tag {
		width: 16px;
		height: 16px;
	}
	
	.art-title {
		border-left: 3px solid #F56C6C;
		padding-left: 5px;
		cursor: pointer;
	}
	
	.art-title:hover {
		padding-left: 10px;
		color: #409EFF;
	}
	
	.art-time {
		margin-right: 20px;
	}
	
	.art-body {
		display: flex;
		padding: 10px 0;
	}
	
	.side-img {
		height: 150px;
		width: 270px;
		overflow: hidden;
		margin-right: 10px;
	}
	
	img.art-banner {
		width: 100%;
		height: 100%;
		transition: all 0.6s;
	}
	
	img.art-banner:hover {
		transform: scale(1.4);
	}
	
	.side-abstract {
		flex: 1;
		display: flex;
		flex-direction: column;
	}
	
	.art-abstract {
		flex: 1;
		color: #aaa;
	}
	
	.art-more {
		height: 40px;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
	
	.art-more .view {
		color: #aaa;
	}
	h5{
		font-size: 18px;
	}
	.pagination {
		background-color: #F9F9F9;
	}
	.tag{
		margin-right: 5px;
	}
</style>