<template>
  <div class="tag">
    <el-card class="box-card">
      <div slot="header" class="d-flex align-items-center">
        <img class="card-icon" src="../assets/biaoqian.png" />
        <span>标签</span>
      </div>
      <div class="text item">
        <el-tag size="mini" class="tag-item" @click="tag(item._id)" v-for="item in data" :key="item._id">{{item.name}}[{{item.articleList}}]</el-tag>
      </div>
    </el-card>
  </div>
</template>

<script>
import {getCategoryNum} from 'network/category.js'

export default {
	name: "tag",
	created() {
		getCategoryNum().then((res) => {
			// console.log(res)
			this.data = res
		})
	},
	data() {
		return {
			data: []
		}
	},
  methods: {
    tag(id) {
      this.$router.push({
        name: "tag",
        params: {
          id
        }
      });
    }
  }
};
</script>

<style scoped>
.box-card .item:hover {
  color: #409eff;
  cursor: pointer;
}

.box-card span {
  font-weight: bold;
}

.card-icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.tag-item {
  margin-right: 10px;
}
</style>